@use '@/styles/mixins/mixins';
@use '@/styles/vars/vars';

.top-antic {

  &__title {
    margin-bottom: 50px - 20px;
  }

  &__body {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .top-card:nth-child(n+5) {
      display: none!important;
    }

    &[data-show] {
      .top-card:nth-child(n+5) {
        display: flex!important;
      }
    }
  }

  &__button {
    margin-top: 50px - 10px;
    width: 380px;
    height: 55px;

    @media (width < vars.$media-1280) {
      height: 45px;
      margin-top: 10px;
    }

    @media (width < vars.$media-768) {
      width: 100%;
      height: 31px;
    }
  }
}
